import {Link} from "react-router-dom";

export default function HomeScreenOld() {
    return (
        <div className="wrapper-content">
            <header className="first-header">
                <div className="header-first-box">
                    <div className="header-first-box-title">
                        ГУО "Средняя школа №35<br/>
                        имени Н.А. Волкова<br/>
                        г. Гродно"
                    </div>
                    <div className="header-first-box-subtitle">
                        МЕТОДИЧЕСКОЕ ОБЪЕДИНЕНИЕ УЧИТЕЛЕЙ<br/>
                        МАТЕМАТИКИ, ИНФОРМАТИКИ И ФИЗИКИ
                    </div>
                    <div className="header-first-box-actions">
                        <a href="http://sch35.oktobrgrodno.gov.by/" className="button">Сайт школы</a>
                    </div>
                </div>
                <div className="header-last-box">
                    <div className="header-last-box-actions">
                        <Link to={`teachers-page`} className={`button`}>Учителя</Link>
                        <Link to={`mathematics-page`} className={`button`}>Математика</Link>
                        <Link to={`informatics-page`} className={`button`}>Информатика</Link>
                        <Link to={`physics-page`} className={`button`}>Физика</Link>
                        <Link to={`useful-links-page`} className={`button`}>Полезные ссылки</Link>
                        <Link to={`mo-page`} className={`button variant-2`}>Работа<span>методического объединения</span></Link>
                    </div>
                </div>

            </header>
        </div>
    )
}